.aboutContainer{
    background-color: #111;
    grid-row: 2/3;
    grid-column: 1/6;
    overflow-y: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.rest-container{
    grid-column: 2/6;
    animation: infoAnimation 2s linear 1 alternate;
    animation-fill-mode: forwards;
}



.info{
    grid-column: 1/2;
    animation: infoAnimation 2s linear 1 alternate;
    animation-fill-mode: forwards;
    overflow:auto;
    
    
    
}

.fall{
    position: absolute;
    top:-7%;
    animation: falling 10s linear infinite normal;
}
.second{
    left: 60%;
    animation-delay: 2s;
    animation-duration: 7s;
}
.third{
    left: 70%;
    animation-delay: 1.5s;
    animation-duration: 9s;
}

.forth{
    left: 85%;
    animation-duration: 9s;
}

@keyframes falling{
    from{ top:-5%;}
    to {top: 105% }
}

.all{
    display:inline-block;
    color:black;
    font-weight: 900;
    
    border-radius: 30px;
    padding: 5px;
    margin: 5px;
    text-align: center;
}

@keyframes infoAnimation {
    from {background-color: transparent;opacity: 0;}
    to {background: rgb(21,22,22);
    background: linear-gradient(0deg, rgba(21,22,22,1) 0%, rgba(13,30,113,1) 100%);opacity: 1;}
  }

  .info-detail{
    display:inline-block; 
      background-color: cornflowerblue;
      font-size: small;
    width: fit-content;
    transition: transform 0.5s;
  }

  .info-detail:hover{
      transform: scale(1.1,1.1);
  }

  .info-header{
      background-color: darkcyan;
      width:95%;
      font-size: medium;
      
  }

  .detail-wrapper{
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;
    height: 89%;
    overflow: hidden;
  }

  @media screen and (min-width: 580px){
    .info-detail{
        font-size: large;
        margin-block-start: auto;
    }
  }

  @media screen and (orientation:landscape){
    .info-detail{
        font-size: small;
        margin-block-start: auto;
    }

    .detail-wrapper{
        height: 130%;
    }
  }